.item {
  display: flex;
  align-items: center;
  width: 200px;
  margin: 5px;
  background: rgb(95, 83, 83);
  padding: 20px;
  border: 1px solid #f1e7e8;
}

.status {
  margin: 0 20px 0 10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.online {
  background-color: green;
}
.offline {
  background-color: red;
}
